import React from "react";
import {
    PageHeader,
    Button,
    Descriptions,
    Switch,
    Icon,
    Col,
    Card,
    Row,
    Divider,
    Input,
    Affix,
    Badge,
     Modal, message
} from 'antd';
import axios from "axios";
import ApiUtil from "../../utils/ApiUtil";
import {isAuthenticated} from "../../utils/Session";
import VideoCompareCard from "../../components/VideoContentDetail/VideoCompareCard";
import moment from 'moment';

let id

class AppealManageDetail extends React.Component {
    state = {
        id: 0,
        video: {},
        copy: {},
        appeal: {},
        pass: false,
        feedback: '',
        visible: false,
        type: 0,
    }

    getData() {
        this.setState({loading: true})
        axios({
                url: ApiUtil.URL_IP + '/api/getAppealDetail/' + id,
                method: 'get'
            }
        ).then(res => {
            this.setState({
                video: res.data.data.video,
                copy: res.data.data.copyinfo,
                appeal: res.data.data.appeal,
                feedback: res.data.data.appeal.appealFeedback,
                pass: res.data.data.appeal.appealResult === 1,
                loading: false,
                type: res.data.data.appeal.appealResult ===null ? 0 : 1
            })
        })
    }

    componentWillMount() {
        id = this.props.match.params.id
        this.setState({id: id})
        this.getData()
    }

    renderResult(result) {
        if (result === null) {
            return '--'
        } else {
            if (result === 1) {
                return <span style={{color: '#5ba730'}}></span>
            } else if (result === 0) {
                return <span style={{color: '#d51468'}}></span>
            }
        }
    }

    handleSubmit() {
        let data = {
            id: this.state.appeal.videoID,
            appealResult: this.state.pass === true ? 1 : 0,
            appealFeedback: this.state.feedback,
            resolver: isAuthenticated(),
            resolveTime: moment().format('YYYY-MM-DD HH:mm:ss'),
            type:this.state.type
        }
        axios({
            url: ApiUtil.URL_IP + '/api/resolveAppeal',
            method: 'post',
            data: data
        }).then(re => {
            this.setState({visible: false})
            if (re.data.code === 0) {
                message.success('')
                this.getData()
            } else {
                message.error('')
            }
        })

    }

    render() {
        return (
            <div>
                <PageHeader
                    ghost={false}
                    onBack={() => window.history.back()}
                    title=""
                    subTitle={id}
                >
                    <Descriptions size="small" column={3}>
                        <Descriptions.Item label="">{this.state.appeal.appealerName}</Descriptions.Item>
                        <Descriptions.Item label="ID">  {this.state.appeal.videoID}</Descriptions.Item>
                        <Descriptions.Item label=" ">{this.state.appeal.appealTime}</Descriptions.Item>
                        <Descriptions.Item label="">{this.state.appeal.state === 1 ?
                            <Badge color="#2db7f5" text=""/> :
                            <Badge color="#87d068" text=""/>}</Descriptions.Item>
                        <Descriptions.Item
                            label=""> {this.state.appeal.resolver === null ? '--' : this.state.appeal.resolver} </Descriptions.Item>
                        <Descriptions.Item
                            label="">{this.state.appeal.resolveTime === 'None' ? '--' : this.state.appeal.resolveTime}</Descriptions.Item>
                        <Descriptions.Item
                            label="">  {this.renderResult(this.state.appeal.appealResult)} </Descriptions.Item>

                    </Descriptions>
                </PageHeader>
                <div style={{marginTop: 10}}>
                    <Row gutter={5}>
                        <Col span={18}>
                            <Card title=''>
                                {this.state.appeal.appealContent}
                            </Card>
                            <Card style={{marginTop: 10}} title=''>
                                {this.state.video.videoname !== undefined ?
                                    <VideoCompareCard id={'3'} video={this.state.video} copy={this.state.copy}/>
                                    :
                                    'Loading...'}
                            </Card>

                        </Col>
                        <Col span={6}>
                            <Affix>
                                <Card title={''}>
                                    <div style={{padding: '10px ', marginTop: -20}}>
                                        
                                        <div style={{padding: 20, marginTop: 10, background: "#f5f5f5"}}>
                                            <div>
                                                ID{this.state.video.id}

                                            </div>
                                            <div style={{marginTop: 5}}>
                                                {isAuthenticated()}
                                            </div>
                                            <div style={{marginTop: 5}}>
                                                {this.state.pass === false ?
                                                <span style={{color: '#d51468'}}></span> :
                                                <span style={{color: '#5ba730'}}></span>}
                                            </div>
                                        </div>
                                    </div>
                                    <Divider/>
                                    <div>
                                         <Switch
                                        checkedChildren={<Icon type="check"/>}
                                        unCheckedChildren={<Icon type="close"/>}
                                        onChange={checked => this.setState({pass: checked})}
                                        checked={this.state.pass}

                                    />
                                    </div>
                                    <div style={{marginTop: 5}}>
                                        <Input.TextArea onChange={event => {
                                        this.setState({feedback: event.target.value})
                                    }} style={{marginTop: 5}} value={this.state.feedback}/>
                                    </div>
                                    <Button style={{marginTop: 10}} type='primary'
                                            onClick={() => this.setState({visible: true})}></Button>

                                </Card>
                            </Affix>
                        </Col>
                    </Row>
                </div>
                <Modal
                    title=''
                    cancelText={''}
                    okText={''}
                    onCancel={() => this.setState({visible: false})}
                    onOk={() => this.handleSubmit()}
                    visible={this.state.visible}
                >
                    <div style={{padding: 20, marginTop: 10, background: "#f5f5f5"}}>
                        <div>
                            ID{this.state.video.id}

                        </div>
                        <div style={{marginTop: 5}}>
                            {this.state.appeal.appealerName}
                        </div>
                        <div style={{marginTop: 5}}>
                            {isAuthenticated()}
                        </div>
                        <div style={{marginTop: 5}}>
                            {this.state.pass === false ?
                            <span style={{color: '#d51468'}}></span> :
                            <span style={{color: '#5ba730'}}></span>}
                        </div>
                    </div>
                </Modal>
            </div>

        );
    }
}

export default AppealManageDetail


